import { Component, h, Host, Prop } from '@stencil/core';
import { generateClasses } from '../../../global/typescript/functions';
import { MtAnos, MtEspecialidades, MtProdutos } from '../../../global/typescript/types';

@Component({
  tag: 'mt-titulo-h1',
  styleUrl: 'titulo-h1.scss',
  scoped: true,
})
export class TituloH1 {
  @Prop({ reflect: true }) produto: MtProdutos;

  @Prop({ reflect: true }) especialidade: MtEspecialidades;

  @Prop({ reflect: true }) ano: MtAnos;

  render() {
    const { produto, especialidade, ano } = this;

    return (
      <Host
        class={generateClasses(produto, especialidade, ano, {
          'mt-titulo-h1': true,
        })}
      >
        <div class="mt-titulo-h1__container">
          <div class="mt-titulo-h1__line"></div>

          <div class="mt-titulo-h1__inner-container">
            <p class="mt-titulo-h1__pre-title">
              <slot name="pre-title"></slot>
            </p>

            <h1 class="mt-titulo-h1__title">
              <slot name="title"></slot>
            </h1>

            <h2 class="mt-titulo-h1__subtitle">
              <slot name="subtitle"></slot>
            </h2>
          </div>
        </div>
      </Host>
    );
  }
}
