$component: --mt-titulo-h1;

:host {
  #{$component}-line-color: var(--mt-color-titulo-capitulo-linha);
  #{$component}-line-width: 8px;
  #{$component}-line-height: auto;
  #{$component}-line-margin: 0 8px 0 0;

  #{$component}-pre-title-color: var(--mt-color-titulo-capitulo-fonte);
  #{$component}-pre-title-font-family: 'Roboto', sans-serif;
  #{$component}-pre-title-font-weight: 100;
  #{$component}-pre-title-text-transform: uppercase;
  #{$component}-pre-title-font-size: 16px;

  #{$component}-title-color: var(--mt-color-titulo-h1-fonte);
  #{$component}-title-font-family: 'Roboto Condensed', sans-serif;
  #{$component}-title-font-weight: 900;
  #{$component}-title-text-transform: uppercase;
  #{$component}-title-font-size: 32px;
  #{$component}-title-margin: 4px 0 4px 0;

  #{$component}-subtitle-color: var(--mt-color-titulo-h2-fonte);
  #{$component}-subtitle-font-family: 'Roboto', sans-serif;
  #{$component}-subtitle-font-weight: 100;
  #{$component}-subtitle-text-transform: uppercase;
  #{$component}-subtitle-font-size: 24px;

  display: block;
}

.mt-titulo-h1 {
  &__container {
    display: flex;
  }

  &__inner-container {
    display: flex;
    flex-direction: column;
  }

  &__line {
    margin: var(#{$component}-line-margin);
    background-color: var(#{$component}-line-color);
    width: var(#{$component}-line-width);
    height: var(#{$component}-line-height);
  }

  &__pre-title {
    color: var(#{$component}-pre-title-color);
    font-family: var(#{$component}-pre-title-font-family);
    font-weight: var(#{$component}-pre-title-font-weight);
    text-transform: var(#{$component}-pre-title-text-transform);
    font-size: var(#{$component}-pre-title-font-size);
    line-height: 1;
    margin: 0;
  }

  &__title {
    color: var(#{$component}-title-color);
    font-family: var(#{$component}-title-font-family);
    font-weight: var(#{$component}-title-font-weight);
    text-transform: var(#{$component}-title-text-transform);
    font-size: var(#{$component}-title-font-size);
    line-height: 1;
    margin: var(#{$component}-title-margin);
  }

  &__subtitle {
    color: var(#{$component}-subtitle-color);
    font-family: var(#{$component}-subtitle-font-family);
    font-weight: var(#{$component}-subtitle-font-weight);
    text-transform: var(#{$component}-subtitle-text-transform);
    font-size: var(#{$component}-subtitle-font-size);
    line-height: 1;
    margin: 0;
  }
}
